@import "_variables.scss";


.footer{
    background-color: $black;
    color: $white;

    button{
        @include w-h(150px,50px);
        font-size: 20px;
        background-color: transparent;
        @include border(1,$white);
        color: $white;
        border-radius: 5px;
        &:hover{
            background-color: $white;
            @include border(0,$white);
            color: $black;
            transition: 1s;
        }
    }
    input{
        @include w-h(100%, 50px)
    }

}

.footerSec{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    h3{
        text-align: left;
        margin-left: 25px;
    }
    ul{
        li{
            list-style: none;
            padding-top: 20px;
            font-size: 20px;
            text-align: left;
        }
    }
}

.sIcons{
    font-size: 40px;
    cursor: pointer;    
}

.fb{
    &:hover{
        color: #3b5998;
        background-color: $white;
        @include w-h(40px,40px);
        transition: 0.5s;
    }
}

.insta{
    &:hover{
        color: #8a3ab9;
        background-color: $white;
        @include w-h(40px,40px);
        transition: 0.5s;
    }
}

.yout{
    &:hover{
        color: #c4302b;
        background-color: $white;
        @include w-h(40px,40px);
        transition: 0.5s;
    }
}

.twit{
    &:hover{
        color: #00acee;
        background-color: $white;
        @include w-h(40px,40px);
        transition: 0.5s;
    }
}